<template>
    <div>
        <header>
            <h1>{{title}}</h1>
        </header>
        <div class="fill"></div>
    </div>
</template>

<script>

export default {
  name: 'Header',
  props: ['title']
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

header{
    border-bottom: 2px solid black;
    position: fixed;
    width: 100%;
    height: 50px;
    background: white;
}

h1{
    padding: 19px 0 9px;
    margin: 0;
    font-size: 24px;
    color: black;
}

.fill{
    height: 50px;
    display: block;
}

</style>



