<template>
  <div>
  <gv-header v-bind:title="msg"></gv-header>
  <a class="big" href="#/create">
    <div class=" create material-icons">create</div>
    <div class="text">Create your pad touching here.</div>
  </a>

  <br />
  <h1>My Pad's</h1>
  <div v-if="withoutIdea">
    <h3>You doesn't have pad's</h3>
  </div>
  <div v-else>
    <div class="idea" v-bind:key="idea" v-for="idea in ideaStore">
      <div class="edit">
        <router-link  class="button" :to="{ name: 'Edit', params:{id:idea.id} }">
          Edit
        </router-link>
        <div v-on:click="deletePad(idea.id, idea.message)" class="button">
          Delete
        </div>
      </div>
      <div class="message">
        {{idea.message}}
      </div>
    </div>
  </div>
  </div>
</template>

<script>

var total = Number(localStorage.getItem('count'))
var ideaStore = []

for (var i = total; i > 0; i--) {
  if (localStorage.getItem('idea-' + i)) {
    ideaStore.push({
      'id': i,
      'message': localStorage.getItem('idea-' + i)
    })
  }
}

export default {
  name: 'Painel',
  data () {
    return {
      msg: 'IdeaPad',
      ideaStore: ideaStore,
      withoutIdea: ideaStore.length > 0 ? 0 : 1
    }
  },

  methods: {
    deletePad: function (id, text) {
      var desejaRemover = confirm('Do you want to remove the pad "' + text + '" ')

      if (desejaRemover) {
        localStorage.removeItem('idea-' + id)
        alert('Pad deleted')
        window.location = '/'
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.big{
  text-align: center;
  display: block;
  color: black;
  text-decoration: none;
  padding: 55px;
  background: rgb(240, 240, 240);
  border-bottom: 2px solid black;
  margin: 10px;
  border-radius: 5px;
}

.big .create{
  font-size:60px;
  display: block;
}

.big .text{
  font-size: 28px;
  width: 200px;
  display: inline-block;
}

.idea{
  text-align: left;
  border-bottom: 2px solid rgb(20, 20, 20);
  margin: 10px;
  border-radius: 5px;
  font-size: 20px;
}

.message{
  padding: 24px;
  background: rgb(240, 240, 240);
}

.button{
  padding: 5px 25px;
  display: inline-block;
  background: rgb(220, 220, 220);
  border-top: 2px solid rgb(20, 20, 20);
  color: black;
  text-decoration: none;
}

</style>



