<template>
  <div>
  <gv-header v-bind:title="msg"></gv-header>
  <textarea id="idea" placeholder="Write here" class="textarea"></textarea>
  <button v-on:click="storeIdea" class="btn">Store!</button>
  </div>
</template>

<script>

export default {
  name: 'Painel',
  data () {
    return {
      msg: 'IdeaPad'
    }
  },

  methods: {
    storeIdea: function (event) {
      localStorage.setItem('count', Number(localStorage.getItem('count')) + 1 || 0)
      localStorage.setItem('idea-' + localStorage.getItem('count'), document.getElementById('idea').value)
      window.location = '/'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.btn{
    background:rgb(20, 20, 20);
    color: white;
    padding: 10px 20px;
    font-size: 28px;
    border: none;
    border-radius: 5px;
}

.textarea{
    display: block;
    margin: 10px;
    min-width: auto;
    max-width: auto;
    border: none;
    height: 200px;
    border-bottom: 2px solid black;
    padding: 15px;
    font-size: 30px;
    outline: none;
}

</style>



